<template>
  <div class="home-page">
    <v-tabs>
      <v-tab>GET TOKEN (AUTH)</v-tab>
      <v-tab>GENERATE TOKEN(UPDATE TOKEN IN DB)</v-tab>
      <v-tab>ADD NEW ACCOUNT</v-tab>
      <v-tab-item>
        <v-card elevation="2">
                <v-text-field label="LOGIN" class="pa-2 ma-2" v-model="login"></v-text-field>
                <v-text-field label="PASSWORD" class="pa-2 ma-2" v-model="password"></v-text-field>
                <v-btn block class="pa-2 ma-2" v-on:click="auth">
                    SEND REQUEST
                </v-btn>
                 <v-textarea
                    counter
                    label="YOUR API TOKEN"
                    :rules="rules"
                    :value="value"
                    class="pa-2 ma-2"
                    v-model="token"
                 >{{this.token}}</v-textarea>
        </v-card>
      </v-tab-item>
      <v-tab-item>
           <v-card elevation="2">
                   <v-text-field label="LOGIN" class="pa-2 ma-2" v-model="login"></v-text-field>
                   <v-text-field label="PASSWORD" class="pa-2 ma-2" v-model="password"></v-text-field>
                   <v-btn block class="pa-2 ma-2" v-on:click="generate">
                       SEND REQUEST
                   </v-btn>
                   <v-textarea
                       counter
                       label="YOUR API TOKEN"
                       :rules="rules"
                       :value="value"
                       class="pa-2 ma-2"
                       v-model="token"
                   >{{this.token}}</v-textarea>
           </v-card>
      </v-tab-item>
      <v-tab-item>
        <v-card elevation="2">
                <v-text-field label="LOGIN" class="pa-2 ma-2" v-model="login"></v-text-field>
                <v-text-field label="PASSWORD" class="pa-2 ma-2" v-model="password"></v-text-field>
                <v-text-field label="api token" class="pa-2 ma-2" v-model="token"></v-text-field>
                <v-btn block class="pa-2 ma-2" v-on:click="addNewAccount">
                    SEND REQUEST
                </v-btn>
        </v-card>
      </v-tab-item>
    </v-tabs>
  </div>
</template>

<script>
  import {mapActions, mapGetters} from 'vuex';
  import axios from 'axios';
  import API from "../api/api-list.js"

  export default {
        name: 'App',
        components: {
        },
        data() {
          return {
             login: null,
             password: null,
             token: null
          }
        },
        computed: {
          ...mapGetters(['HOSTS'])
        },
        methods: {
          ...mapActions(['GET_HOSTS_FROM_API','AUTH']),
          auth(){
            let login = this.login;
            let password = this.password;
            this.AUTH({login,password});
            this.token = this.$store.state.token;

          },
          generate(){
            axios(API.generateToken, {
                    headers: {
                                "Content-type": "application/json",
                                "token" : this.token
                             },
                    method: "POST",
                    data: {
                       login: this.login,
                       password: this.password
                    }
            }).then(response=>{
                 console.log(response);
                 this.token = response.data.token;
                 return response;
            });
          },
          addNewAccount(){
            axios(API.addUser, {
                    headers: {
                                "Content-type": "application/json",
                                "token" : this.token
                             },
                    method: "POST",
                    data: {
                       login: this.login,
                       password: this.password
                    }
            }).then(response=>{
                 console.log(response);
                 this.token = response.data.token;
                 return response;
            });
          }
        },
        mounted(){
          this.GET_HOSTS_FROM_API();
        }
  }
</script>