<template>
      <v-card fluid>
        <v-expansion-panels>
            <v-expansion-panel>
              <v-expansion-panel-header>
                ACTIONS
              </v-expansion-panel-header>
              <v-expansion-panel-content>
                    <v-card>
                        <v-tabs>
                          <v-tab>Details</v-tab>
                          <v-tab>Delete host</v-tab>
                          <v-tab>Add new host</v-tab>

                          <v-tab-item>
                             <v-card flat>
                                <p class="pa-2 ma-2">ID: {{c_host_id}}</p>
                                <p class="pa-2 ma-2">Creator: {{c_host_creator}}</p>
                                <p class="pa-2 ma-2">Create date: {{c_host_date}}</p>
                                <v-text-field label="Host ip" class="pa-2 ma-2" v-model="c_host_ip"></v-text-field>
                                <v-text-field label="DNS name" class="pa-2 ma-2" v-model="c_host_dns"></v-text-field>
                                <v-text-field label="ssh user" class="pa-2 ma-2" v-model="c_host_user"></v-text-field>
                                <v-text-field label="ssh password" class="pa-2 ma-2" v-model="c_host_pass"></v-text-field>
                                <v-textarea
                                   counter
                                   label="Description"
                                   :rules="rules"
                                   :value="value"
                                   class="pa-2 ma-2"
                                   v-model="c_host_desc"
                                >{{c_host_desc}}</v-textarea>
                                <v-text-field label="Used" class="pa-2 ma-2" v-model="c_host_used"></v-text-field>
                                <v-text-field label="token for api" class="pa-2 ma-2" v-model="token"></v-text-field>
                                <v-btn block class="pa-2 ma-2" v-on:click="updateHostDataOnHost">
                                      UPDATE DATA ON SERVER
                                </v-btn>
                             </v-card>
                          </v-tab-item>
                          <v-tab-item>
                             <v-card flat>
                                <v-text-field label="Host id" class="pa-2 ma-2" v-model="destroy_host_id"></v-text-field>
                                <v-text-field label="token for api" class="pa-2 ma-2" v-model="token"></v-text-field>
                                <v-btn block class="pa-2 ma-2" v-on:click="deleteHost">
                                   SEND REQUEST
                                </v-btn>
                             </v-card>
                          </v-tab-item>
                          <v-tab-item>
                             <v-card flat>
                               <v-text-field label="Host ip" class="pa-2 ma-2" v-model="add_host_ip"></v-text-field>
                               <v-text-field label="DNS name" class="pa-2 ma-2" v-model="add_host_dns"></v-text-field>
                               <v-text-field label="ssh user" class="pa-2 ma-2" v-model="add_host_user"></v-text-field>
                               <v-text-field label="ssh pass" class="pa-2 ma-2" v-model="add_host_pass"></v-text-field>
                               <v-textarea
                                    counter
                                    label="Description"
                                    :rules="rules"
                                    :value="value"
                                    class="pa-2 ma-2"
                                    v-model="add_host_desc"
                               ></v-textarea>
                               <v-text-field label="token for api" class="pa-2 ma-2" v-model="token"></v-text-field>
                               <v-btn block class="pa-2 ma-2" v-on:click="addNewHost">
                                   SEND REQUEST
                               </v-btn>
                             </v-card>
                          </v-tab-item>
                        </v-tabs>

                  </v-card>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        <v-card-title>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-data-table :headers="headers" :items="this.$store.state.hosts" :items-per-page="10" class="elevation-1" :search="search">
           <template v-slot:item.action="{item}">
             <v-btn color="primary" elevation="2" @click="getDetails(item.id)">Details</v-btn>
           </template>
        </v-data-table>
      </v-card>
</template>

<script>
  import {mapActions, mapGetters} from 'vuex';
  import axios from 'axios';
  import API from "../api/api-list.js"

  export default {
        name: 'App',
        components: {
        },
        data() {
          return {
             search: '',
             headers:[
                 {text: 'id', value: 'id'},
                 {text: 'Host', value: 'host'},
                 {text: 'DNS name', value: 'dnsName'},
                 {text: 'Creator', value: 'creator'},
                 {text: 'CreateDate', value: 'createDate'},
                 {text: 'Used', value: 'used'},
                 {text: 'Actions',value: 'action'},
             ],

             destroy_host_id: '-1000',
             token: null,

             add_host_ip: null,
             add_host_dns: null,
             add_host_desc: null,
             add_host_user: null,
             add_host_pass: null,

             d_host_ip: null,
             d_host_dns: null,
             d_host_desc: null,
             d_host_user: null,
             d_host_pass: null,
             d_host_create_date: null,
             d_host_creator: null,
             d_host_used: null,

             c_host_id: null,
             c_host_ip: null,
             c_host_dns: null,
             c_host_user: null,
             c_host_pass: null,
             c_host_desc: null,
             c_host_creator: null,
             c_host_date: null,
             c_host_used: null

          }
        },
        computed: {
          ...mapGetters(['HOSTS','TOKEN'])
        },
        methods: {
          ...mapActions(['GET_HOSTS_FROM_API', 'MODIFY_TOKEN']),
          deleteHost(){
               this.MODIFY_TOKEN(this.token)
               let resultUrl = API.hostDestroyUrl + this.destroy_host_id
               return axios(resultUrl, {
                 headers: {
                            "Content-type": "application/json",
                            "token" : this.token
                          },
                 method: "DELETE",
                }).then(response=>{
                    console.log(response);
                    return response;
                });

          },
          addNewHost(){
             axios(API.addHostUrl, {
                    headers: {
                                    "Content-type": "application/json",
                                    "token" : this.token
                             },
                    method: "POST",
                    data: {
                        host: this.add_host_ip,
                        dnsName: this.add_host_dns,
                        ssh_user: this.add_host_user,
                        ssh_pass: this.add_host_pass,
                        description: this.add_host_desc
                    }
             }).then(response=>{
                 console.log(response);
                 return response;
             });
          },
          getDetails(id){
                console.log(id);
                let resultUrl = API.hostInfo + id;
                axios(resultUrl, {
                    headers: {
                                "Content-type": "application/json",
                             },
                    method: "GET",
                }).then(response=>{
                   console.log(response);

                   this.c_host_id = response.data.id;
                   this.c_host_ip = response.data.host;
                   this.c_host_dns = response.data.dnsName;
                   this.c_host_user = response.data.ssh_user;
                   this.c_host_pass = response.data.ssh_pass;
                   this.c_host_desc = response.data.description;
                   this.c_host_creator = response.data.creator;
                   this.c_host_date = response.data.createDate;
                   this.c_host_used = response.data.used;

                   return response;
                });
          },
          updateHostDataOnHost(){
            let resultUrl = API.hostInfo;
            axios(resultUrl, {
                    headers: {
                                    "Content-type": "application/json",
                                    "token" : this.token
                             },
                    method: "POST",
                    data: {
                        id: this.c_host_id,
                        host: this.c_host_ip,
                        dnsName: this.c_host_dns,
                        ssh_user: this.c_host_user,
                        ssh_pass: this.c_host_pass,
                        description: this.c_host_desc,
                        creator: this.c_host_creator,
                        createDate: this.c_host_createDate,
                        used: this.used
                    }
             }).then(response=>{
                 console.log(response);
                 return response;
             });
          }
        },
        mounted(){
          this.GET_HOSTS_FROM_API();
        }
  }
</script>