<template>
      <v-card fluid>
        <v-expansion-panels>
            <v-expansion-panel>
              <v-expansion-panel-header>
                ACTIONS
              </v-expansion-panel-header>
              <v-expansion-panel-content>
                    <v-card>
                        <v-tabs>
                          <v-tab>Delete Cluster</v-tab>
                          <v-tab>Details</v-tab>
                          <v-tab>Create Cluster</v-tab>
                          <v-tab-item>
                             <v-card flat>
                                <v-text-field label="Cluster name" class="pa-2 ma-2" v-model="destroy_cluster"></v-text-field>
                                <v-text-field label="token for api" class="pa-2 ma-2" v-model="token"></v-text-field>
                                <v-btn block class="pa-2 ma-2" v-on:click="destroyCluster">
                                   SEND REQUEST
                                </v-btn>
                             </v-card>
                          </v-tab-item>
                          <v-tab-item>
                             <v-card flat>
                               <p class="pa-2 ma-2">ID: {{c_cluster_id}}</p>
                               <p class="pa-2 ma-2">Name: {{c_clusterName}}</p>
                               <p class="pa-2 ma-2">Creator: {{c_cluster_creator}}</p>
                               <p class="pa-2 ma-2">Create date: {{c_cluster_create_date}}</p>
                               <p class="pa-2 ma-2">Destroy date: {{c_cluster_destroy_date}}</p>
                               <p class="pa-2 ma-2">Description: {{c_cluster_desc}}</p>

                             </v-card>
                          </v-tab-item>
                          <v-tab-item>
                             <v-card flat>
                               <v-text-field label="ID" class="pa-2 ma-2" v-model="c_cluster_id"></v-text-field>
                               <v-text-field label="Cluster name" class="pa-2 ma-2" v-model="c_clusterName"></v-text-field>
                               <v-text-field label="Creator" class="pa-2 ma-2" v-model="hosts"></v-text-field>
                               <v-text-field label="Reserve time" class="pa-2 ma-2" v-model="reserve_time"></v-text-field>
                               <v-text-field label="type (MINUTES,HOURS,DAYS)" class="pa-2 ma-2" v-model="reserve_type"></v-text-field>
                               <v-textarea
                                    counter
                                    label="Description"
                                    :rules="rules"
                                    :value="value"
                                    class="pa-2 ma-2"
                                    v-model="description"
                               ></v-textarea>
                               <v-text-field label="token for api" class="pa-2 ma-2" v-model="token"></v-text-field>
                               <v-btn block class="pa-2 ma-2" v-on:click="createCluster">
                                   SEND REQUEST
                               </v-btn>
                             </v-card>
                          </v-tab-item>
                        </v-tabs>

                  </v-card>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        <v-card-title>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-data-table :headers="headers" :items="this.$store.state.clusters" :items-per-page="10" class="elevation-1" :search="search">
           <template v-slot:item.action="{item}">
             <v-btn color="primary" elevation="2" @click="getDetails(item.clusterName)" pa-2>Details</v-btn>
           </template>
        </v-data-table>
      </v-card>
</template>

<script>
  import {mapActions, mapGetters} from 'vuex';
  import axios from 'axios';
  import API from "../api/api-list.js"

  export default {
    data() {
      return {
        search: '',
        headers:[
            {text: 'id', value: 'id'},
            {text: 'Cluster name', value: 'clusterName'},
            {text: 'Creator', value: 'creator'},
            {text: 'Create Date', value: 'createDate'},
            {text: 'Destroy date', value: 'destroyDate'},
            {text: 'Actions',value: 'action'},
        ],
        destroy_cluster: null,
        token: null,

        size: null,
        cluster_name: null,
        hosts: '',
        reserve_time: null,
        reserve_type: null,
        description: null,

        c_clusterName: null,
        c_cluster_id: null,
        c_cluster_desc: null,
        c_cluster_creator: null,
        c_cluster_used: null,
        c_cluster_hosts: null,
        c_cluster_create_date: null,
        c_cluster_destroy_date: null

      }
    },
    computed: {
        ...mapGetters(['CLUSTERS'])
    },
    methods: {
        ...mapActions(['GET_CLUSTERS_FROM_API']),
        createCluster(){

            console.log(this.hosts);
            console.log(this.size);
            console.log(this.cluster_name);
            console.log(this.description);
            console.log(this.reserve_time);
            console.log(this.reserve_type);

            axios(API.clusterCreate, {
                    headers: {
                                    "Content-type": "application/json",
                                    "token" : this.token
                             },
                    method: "POST",
                    data: {
                        host: this.hosts,
                        size: this.size,
                        name: this.cluster_name,
                        desc: this.description,
                        time: this.reserve_time,
                        type: this.reserve_type
                    }
             }).then(response=>{
                 console.log(response);
                 return response;
             });
        },
        destroyCluster(){
            let destroyUrl = API.destroyClusterUrl + this.destroy_cluster
            axios(destroyUrl, {
                    headers: {
                                    "Content-type": "application/json",
                                    "token" : this.token
                             },
                    method: "POST",

             }).then(response=>{
                 console.log(response);
                 return response;
             });
        },
        getDetails(name){
            let resultUrl = API.clusterDetails + name;
             axios(resultUrl, {
                                headers: {
                                            "Content-type": "application/json",
                                         },
                                method: "GET",
                            }).then(response=>{
                               console.log(response);

                               this.c_clusterName = response.data.clusterName;
                               this.c_cluster_id = response.data.id;
                               this.c_cluster_creator = response.data.creator;
                               this.c_cluster_desc = response.data.description;
                               this.c_cluster_create_date = response.data.createDate;
                               this.c_cluster_destroy_date = response.data.destroyDate;

                               return response;
                            });
        }

    },
    mounted(){
        this.GET_CLUSTERS_FROM_API();
    }
  }
</script>