import Vue from 'vue';
import Vuex from 'vuex';
import axios from "axios";
import API from "../api/api-list.js"

Vue.use(Vuex);

let store = new Vuex.Store({
    state: {
        token: 'default',
        login: null,
        hosts: [],
        clusters: [],
        apis: []
    },
    mutations: {
        SET_HOSTS_TO_STATE: (state, hosts)=>{
            state.hosts = hosts;
        },
        SET_LOGIN_TO_STATE: (state, login)=>{
            state.login = login;
        },
        SET_CLUSTERS_TO_STATE: (state, clusters)=>{
            state.clusters = clusters;
        },
        SET_TOKEN: (state, token)=>{
            state.token = token;
        },
        SET_API_TO_STATE: (state, apis)=>{
            state.apis = apis;
        }
    },
    actions: {
        GET_HOSTS_FROM_API({commit}){
            return axios(API.hostsUrl, {
                headers: {
                    "Content-type": "application/json",
                },
                method: "GET"
            }).then(hosts =>{

                console.log(hosts);
                commit('SET_HOSTS_TO_STATE', hosts.data.hosts);
                return hosts;
            }).catch(error=> {
                console.log(error);
            })
        },
        GET_CLUSTERS_FROM_API({commit}){
            return axios(API.clusterUrl, {
                headers: {
                    "Content-type": "application/json",
                },
                method: "GET"
            }).then(clusters=>{
                console.log(clusters);
                commit('SET_CLUSTERS_TO_STATE', clusters.data);
                return clusters;
            }).catch(error => {
                console.log(error);
            })
        },
        AUTH({commit},{login, password}){
            console.log(login);
            console.log(password);
            return axios(API.authUrl, {
                 headers: {
                    "Content-type": "application/json",
                 },
                 method: "POST",
                 data: {
                    login: login,
                    password: password
                 }
            }).then(token=> {
                console.log(token);
                commit('SET_LOGIN_TO_STATE', login);
                commit('SET_TOKEN', token.data.token);
                return token.data;
            })
        },
        MODIFY_TOKEN({commit}, token){
            commit('SET_TOKEN', token);
        },
        GET_INFO_FROM_API({commit}){
                    return axios(API.infoUrl, {
                        headers: {
                            "Content-type": "application/json",
                        },
                        method: "GET"
                    }).then(api=>{
                        console.log(api);
                        commit('SET_API_TO_STATE', api.data.API);
                        return api.data.API;
                    }).catch(error => {
                        console.log(error);
                    })
        },
    },
    getters: {
        HOSTS(state){
            return state.hosts;
        },
        CLUSTERS(state){
            return state.clusters;
        },
        TOKEN(state){
            return state.token
        },
        LOGIN(state){
            return state.login
        },
        API(state){
            return state.apis
        }
    }
})

export default store;